import React from 'react';
import styled, { css, keyframes } from 'styled-components';
import { styleHelpers } from '../../../styles';

const placeHolderShimmer = keyframes`
  0%{
      background-position: -468px 0
  }
  100%{
      background-position: 468px 0
  }
`;

const shimmerAnimationStyles = ({ theme }) => css`
  background: linear-gradient(
    90deg,
    ${theme.colors.p500} 8%,
    ${theme.colors.p600} 18%,
    ${theme.colors.p500} 33%
  );
  animation: ${placeHolderShimmer} linear infinite 1s forwards;
  background-size: 800px 104px;
`;

const Container = styled('div')`
  display: flex;
  align-items: flex-start;
  margin: 0 25px;
  width: 100%;

  ${styleHelpers.media.medium`
     width: initial;
     min-width: 250px;
  `};
`;

const iconStyles = ({ theme }) => css`
  background: ${theme.colors.p500};
  margin: 10px;
  width: 55px;
  height: 42px;
  border-radius: 4px;
  ${shimmerAnimationStyles}
`;

const Icon = styled('div')`
  ${iconStyles}
`;

const descriptionStyles = css`
  display: flex;
  flex-direction: column;
  justify-content: center;
`;

const Description = styled('div')`
  ${descriptionStyles}
`;

const titleStyles = ({ theme }) => css`
  width: 120px;
  height: 20px;
  margin: 10px 0 15px 0;
  background: ${theme.colors.p600};
  ${shimmerAnimationStyles}
`;

const Title = styled('span')`
  ${titleStyles}
`;

const postTitleStyles = ({ theme }) => css`
  width: 90px;
  height: 10px;
  background: ${theme.colors.p400};
  margin-bottom: 10px;
  ${shimmerAnimationStyles}
`;

const PostTitle = styled('span')`
  ${postTitleStyles}
`;

const postDescriptionStyles = ({ theme }) => css`
  width: 70px;
  height: 10px;
  background: ${theme.colors.p400};
  ${shimmerAnimationStyles}
`;

const PostDescription = styled('span')`
  ${postDescriptionStyles}
`;

function LatestPost() {
  return (
    <Container>
      <Icon />
      <Description>
        <Title />
        <PostTitle />
        <PostDescription />
      </Description>
    </Container>
  );
}

export default LatestPost;
