import PropTypes from 'prop-types';
import React from 'react';
import styled, { css } from 'styled-components';

import Menu from '../Menu';
import DarkModeToggle from '../DarkModeToggle';
import { styleHelpers } from '../../styles/';

const containerStyles = ({ theme }) => css`
  display: flex;
  justify-content: space-between;
  width: 100%;
  padding: 50px 25px;
  top: 0;
  right: 0;
  left: 0;
  z-index: 1;
  background-color: ${theme.colors.p000};
  max-width: 960px;
  transition: color ${theme.transitions.default},
    background ${theme.transitions.default};
  min-height: 10vh;

  ${styleHelpers.media.medium`
    width: 100%;
    padding: 50px;
    justify-content: flex-end;
  `};

  ${styleHelpers.media.large`
    padding: 50px 0;
    margin: 0 auto;
  `};
`;

const Container = styled('header')`
  ${containerStyles}
`;

function Header({ location, onModeSwitch, isDark }) {
  return (
    <Container>
      <Menu location={location} />
      <DarkModeToggle onModeSwitch={onModeSwitch} isDark={isDark} />
    </Container>
  );
}

Header.propTypes = {
  location: PropTypes.object.isRequired,
  onModeSwitch: PropTypes.func.isRequired,
  isDark: PropTypes.bool.isRequired,
};

export default Header;
