import React, { useState } from 'react';
import styled, { css, keyframes } from 'styled-components';
import { Link } from 'gatsby';
import { styleHelpers } from '../../styles';
import MobileMenuButton from './MobileMenu/MobileMenuButton';
import MobileMenu from './MobileMenu';

const Nav = styled('nav')`
  display: flex;
  align-items: center;
  height: 100%;
  margin-right: 20px;

  ${styleHelpers.media.medium`
    margin-right: 50px;
  `};
`;

const animateWidth = keyframes`
  from {
    width: 0;
  }

  to {
    width: 100%;
  }
`;

const selectedItemStyles = ({ to, pathname, theme }) =>
  to === pathname &&
  css`
    color: ${theme.colors.p600};
    :after {
      content: ' ';
      animation: ${animateWidth} cubic-bezier(0.715, 0.41, 0.355, 1) 0.8s
        forwards;
      position: absolute;
      bottom: 4px;
      border-bottom: 2px solid ${theme.colors.p600};
    }
  `;

const itemStyles = ({ theme }) => css`
  position: relative;
  display: flex;
  align-items: center;
  color: ${theme.colors.p200};
  margin: 0px 0.5rem;
  text-decoration: none;
  outline-width: 0;
  transition: color 250ms cubic-bezier(0.4, 0, 0.2, 1) 0s;
  letter-spacing: 0.075em;
  font-size: 30px;
  font-weight: 300;
  box-shadow: none;
  :hover {
    color: ${theme.colors.p600};
  }

  ${styleHelpers.media.medium`
    font-size: 20px;
    height: 100%;
  `};
`;

const Item = styled(Link)`
  ${itemStyles} ${selectedItemStyles}
`;

const NavItems = styled('div')`
  display: none;

  ${styleHelpers.media.medium`
    display: flex;
    flex-direction: row;
  `};
`;

const routes = [
  { value: '/', label: 'Home' },
  { value: '/about/', label: 'About' },
  { value: '/blog/', label: 'Blog' },
];

function Menu({ location, onMenuToggle }) {
  const [isMobileMenuOpen, setIsOpen] = useState(false);

  const items = routes.map(({ value, label }) => (
    <Item
      key={value}
      pathname={location.pathname}
      to={value}
      onClick={() => isMobileMenuOpen && setIsOpen(false)}
    >
      {label}
    </Item>
  ));

  return (
    <Nav>
      <MobileMenuButton onMenuToggle={() => setIsOpen(!isMobileMenuOpen)} />
      <MobileMenu
        isDisplayed={isMobileMenuOpen}
        onMenuClose={() => setIsOpen(false)}
        items={items}
      />
      <NavItems>{items}</NavItems>
    </Nav>
  );
}

export default Menu;
