import React from 'react';
import styled, { css } from 'styled-components';
import Switch from 'react-switch';
import { Moon, Sun } from './Icons';

const labelStyles = css`
  display: flex;
  align-items: center;
`;

const Label = styled('label')`
  ${labelStyles}
`;

function DarkModeToggle({ onModeSwitch, isDark }) {
  return (
    <Label>
      <Switch
        checkedIcon={<Moon />}
        uncheckedIcon={<Sun />}
        offColor="#000"
        onColor="#000"
        onChange={onModeSwitch}
        checked={isDark}
        width={50}
        height={24}
        activeBoxShadow={'0 0 2px 3px #02a287'}
      />
    </Label>
  );
}

export default DarkModeToggle;
