import React from 'react';
import styled, { css } from 'styled-components';

const footerStyles = ({ theme }) => css`
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background: ${theme.colors.p100};
  transition: color ${theme.transitions.default},
    background ${theme.transitions.default};
  padding: 2em 0 1em 0;
  margin-top: 40px;
  width: 100%;
  min-height: 30vh;
  p {
    text-align: center;
    color: ${theme.colors.p200};
    font-size: 12px;
    font-weight: 400;
    margin: 0;
    padding: 2.5em 0 0;
  }
`;

const StyledFooter = styled('footer')`
  ${footerStyles}
`;

function Footer({ children }) {
  return (
    <StyledFooter>
      {children}
      <p>© {new Date().getFullYear()} Guy Livni. All rights reserved.</p>
    </StyledFooter>
  );
}

export default Footer;
