import { css } from 'styled-components';

export const media = {
  small: (...args) => css`
    @media (min-width: 480px) {
      ${css(...args)};
    }
  `,
  medium: (...args) => css`
    @media (min-width: 768px) {
      ${css(...args)};
    }
  `,
  large: (...args) => css`
    @media (min-width: 960px) {
      ${css(...args)};
    }
  `,
  huge: (...args) => css`
    @media (min-width: 1280px) {
      ${css(...args)};
    }
  `,
};
