import React from 'react';
import styled, { css } from 'styled-components';
import { GoOctoface } from 'react-icons/go';
import { styleHelpers } from '../../../styles';

const containerStyles = css`
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  width: 100%;
  margin: 0 25px 20px 25px;

  ${styleHelpers.media.medium`
     justify-content: center;
     margin: 0 25px;
     width: initial;
     min-width: 250px;
  `};
`;

const Container = styled('div')`
  ${containerStyles}
`;

const githubIconStyles = ({ theme }) => css`
  font-size: 60px;
  color: ${theme.colors.p500};
  margin: 0 10px;
`;

const GithubIcon = styled(GoOctoface)`
  ${githubIconStyles}
`;

const titleStyles = ({ theme }) => css`
  font-size: 20px;
  font-weight: 400;
  color: ${theme.colors.p600};
`;

const Title = styled('div')`
  ${titleStyles}
`;

const hrefBaseStyles = ({ theme }) => css`
  color: ${theme.colors.p400};
  box-shadow: none;
  font-weight: 300;
  font-size: 14px;
  max-width: 200px;
  text-decoration: underline;
  transition: color 250ms cubic-bezier(0.4, 0, 0.2, 1) 0s;

  :hover {
    color: ${theme.colors.p600};
    text-decoration: none;
  }
`;

const CommitTitle = styled('a')`
  ${hrefBaseStyles}
  font-weight: 400;
`;

const CommitRepo = styled('a')`
  ${hrefBaseStyles}
  font-size: 12px;
`;

const Description = styled('div')`
  display: flex;
  flex-direction: column;
`;

function LatestCommit({ commit, repo }) {
  return (
    <Container>
      <GithubIcon />
      <Description>
        <Title>Latest Commit</Title>
        <CommitTitle target="_blank" href={commit.url}>
          {commit.message}
        </CommitTitle>
        <CommitRepo target="_blank" href={repo.url}>
          in {repo.name}
        </CommitRepo>
      </Description>
    </Container>
  );
}

export default LatestCommit;
