import React from 'react';
import styled, { css } from 'styled-components';
import { GoBook } from 'react-icons/go';
import { styleHelpers } from '../../../styles';

const Container = styled('div')`
  display: flex;
  align-items: flex-start;
  margin: 0 25px;
  width: 100%;

  ${styleHelpers.media.medium`
     width: initial;
     min-width: 250px;
  `};
`;

const goBookIconStyles = ({ theme }) => css`
  font-size: 60px;
  color: ${theme.colors.p500};
  margin: 0 10px;
`;

const GoBookIcon = styled(GoBook)`
  ${goBookIconStyles}
`;

const descriptionStyles = css`
  display: flex;
  flex-direction: column;
  justify-content: center;
`;

const Description = styled('div')`
  ${descriptionStyles}
`;

const titleStyles = ({ theme }) => css`
  font-size: 20px;
  font-weight: 400;
  color: ${theme.colors.p600};
`;

const Title = styled('div')`
  ${titleStyles}
`;

const postTitleStyles = ({ theme }) => css`
  color: ${theme.colors.p400};
  box-shadow: none;
  font-weight: 300;
  width: fit-content;
  font-size: 14px;
  font-weight: 400;
  text-decoration: underline;
  transition: color 250ms cubic-bezier(0.4, 0, 0.2, 1) 0s;

  :hover {
    color: ${theme.colors.p600};
    text-decoration: none;
  }
`;

const PostTitle = styled('a')`
  ${postTitleStyles}
`;

const postDescriptionStyles = ({ theme }) => css`
  color: ${theme.colors.p400};
  font-size: 12px;
  font-weight: 300;
`;

const PostDescription = styled('span')`
  ${postDescriptionStyles}
`;

function LatestPost() {
  return (
    <Container>
      <GoBookIcon />
      <Description>
        <Title>Latest Writing</Title>
        <PostTitle>Latest Post Title</PostTitle>
        <PostDescription>Latest description</PostDescription>
      </Description>
    </Container>
  );
}

export default LatestPost;
