import React from 'react';
import styled from 'styled-components';

import LatestCommit from './LatestCommit';
import LatestPost from './LatestPost';
import Fetcher from '../Fetcher/Fetcher';
import LatestPlaceholder from './LatestPlaceholder';

import * as GithubService from '../../services/github';
import * as githubApi from '../../api/github';

const Container = styled('div')`
  display: flex;
  margin-bottom: 30px;
  flex-wrap: wrap;
  justify-content: center;
`;

function Latest() {
  return (
    <Container>
      <Fetcher
        action={githubApi.getUserEvents('GuyLivni')}
        loader={<LatestPlaceholder />}
      >
        {events => {
          const { commit, repo } = GithubService.getLatestCommit(events);
          return <LatestCommit commit={commit} repo={repo} />;
        }}
      </Fetcher>

      <LatestPost />
    </Container>
  );
}

export default Latest;
